/* SajuResult.css */
.saju-result {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
}

.result-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.result-header h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.result-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.share-button,
.back-button {
  padding: 12px 24px;
  border: none;
  border-radius: 25px;
  font-size: 1.1em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
}

.share-button {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

.back-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.share-button:hover,
.back-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.result-content {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 30px;
}

.basic-info {
  margin-bottom: 30px;
}

.basic-info h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.info-item .label {
  font-weight: 600;
  opacity: 0.9;
}

.info-item .value {
  font-weight: 700;
  color: #ffffff;
}

.saju-table {
  margin-bottom: 30px;
}

.saju-table h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.pillars {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

.pillar {
  background: rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  border: 2px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.pillar:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.pillar-header {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 15px;
  color: #ffffff;
  opacity: 0.9;
}

.pillar-content {
  margin-bottom: 15px;
}

.gan,
.ji {
  display: inline-block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  margin: 5px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 50%;
  font-size: 1.5em;
  font-weight: 700;
  color: white;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

.pillar-animal {
  font-size: 1.1em;
  font-weight: 600;
  color: #ffffff;
  opacity: 0.9;
}

.fortune-content {
  margin-top: 30px;
}

.fortune-content h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.fortune-text {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  line-height: 1.8;
  font-size: 1.1em;
  white-space: pre-wrap;
}

.fortune-line {
  margin-bottom: 10px;
  opacity: 0.95;
}

.fortune-line:empty {
  margin-bottom: 20px;
}

.result-footer {
  margin-top: 30px;
}

.disclaimer {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 20px;
  text-align: center;
}

.disclaimer p {
  margin: 0;
  font-size: 1em;
  line-height: 1.6;
  opacity: 0.9;
}

.action-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.try-again-button,
.share-button-large {
  padding: 15px 30px;
  border: none;
  border-radius: 25px;
  font-size: 1.2em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
  min-width: 200px;
}

.try-again-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.share-button-large {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

.try-again-button:hover,
.share-button-large:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .saju-result {
    margin: 10px;
    padding: 15px;
  }
  
  .result-header h2 {
    font-size: 2em;
  }
  
  .result-content {
    padding: 20px;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
  }
  
  .pillars {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  
  .pillar {
    padding: 15px;
  }
  
  .gan,
  .ji {
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 1.3em;
  }
  
  .fortune-text {
    padding: 20px;
    font-size: 1em;
  }
  
  .action-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .try-again-button,
  .share-button-large {
    width: 100%;
    max-width: 300px;
  }
  
  .result-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .share-button,
  .back-button {
    width: 100%;
    max-width: 200px;
  }
} 