/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  color: #333;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-header {
  text-align: center;
  padding: 40px 20px 20px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.app-header h1 {
  font-size: 3em;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  color: white;
  font-weight: 700;
}

.app-header p {
  font-size: 1.3em;
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.app-container {
  flex: 1;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.tab-menu {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  padding: 10px;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
}

.tab-button {
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  color: white;
  font-size: 1em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  white-space: nowrap;
  min-width: 120px;
  text-align: center;
  flex-shrink: 0;
}

.tab-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.2);
}

.tab-button.active {
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  transform: translateY(-2px);
}

/* 색깔 성격 테스트 탭 특별 스타일 */
.tab-button:last-child {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-color: rgba(255, 255, 255, 0.4);
  position: relative;
  overflow: hidden;
}

.tab-button:last-child::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s ease;
}

.tab-button:last-child:hover::before {
  left: 100%;
}

.main-content {
  margin-bottom: 30px;
}

.app-footer {
  text-align: center;
  padding: 30px 20px;
  background: rgba(0, 0, 0, 0.3);
  color: rgba(255, 255, 255, 0.8);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.app-footer p {
  margin-bottom: 10px;
  font-size: 1em;
}

.app-footer small {
  font-size: 0.9em;
  opacity: 0.8;
}

/* 반짝이는 효과 */
@keyframes twinkle {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.3; }
}

.app-header h1::before {
  content: "✨";
  animation: twinkle 2s ease-in-out infinite;
  margin-right: 10px;
}

.app-header h1::after {
  content: "✨";
  animation: twinkle 2s ease-in-out infinite 1s;
  margin-left: 10px;
}

/* 로딩 애니메이션 */
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loading-content {
  text-align: center;
  color: white;
  animation: pulse 2s infinite;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .app-header {
    padding: 20px 15px 15px;
  }
  
  .app-header h1 {
    font-size: 2.2em;
  }
  
  .app-header p {
    font-size: 1.1em;
  }
  
  .app-container {
    padding: 15px;
  }
  
  .tab-menu {
    gap: 8px;
    padding: 5px;
    flex-wrap: nowrap;
    overflow-x: auto;
    justify-content: flex-start;
    padding: 10px 5px;
  }
  
  .tab-button {
    padding: 10px 12px;
    font-size: 0.9em;
    min-width: 110px;
    flex-shrink: 0;
  }
  
  .app-footer {
    padding: 20px 15px;
  }
  
  .app-header h1::before,
  .app-header h1::after {
    display: none;
  }
}

/* 초소형 모바일 (320px 이하) */
@media (max-width: 480px) {
  .tab-menu {
    gap: 5px;
    padding: 10px 5px;
    overflow-x: auto;
    justify-content: flex-start;
  }
  
  .tab-button {
    padding: 8px 10px;
    font-size: 0.8em;
    min-width: 90px;
    flex-shrink: 0;
  }
  
  .app-header h1 {
    font-size: 1.8em;
  }
  
  .app-header p {
    font-size: 1em;
  }
}

/* 태블릿 크기 */
@media (min-width: 769px) and (max-width: 1024px) {
  .tab-button {
    padding: 14px 24px;
    font-size: 1.05em;
  }
  
  .tab-menu {
    gap: 18px;
  }
}

/* 다크 테마 스크롤바 */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* 접근성 개선 */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* 포커스 스타일 */
.tab-button:focus {
  outline: 2px solid rgba(255, 255, 255, 0.8);
  outline-offset: 2px;
}

/* 탭 메뉴 스크롤 가능하게 (극소형 화면용) */
@media (max-width: 375px) {
  .tab-menu {
    gap: 3px;
    padding: 10px 3px;
    overflow-x: auto;
    justify-content: flex-start;
    -webkit-overflow-scrolling: touch;
  }
  
  .tab-button {
    padding: 6px 8px;
    font-size: 0.75em;
    min-width: 80px;
    flex-shrink: 0;
  }
  
  .tab-menu::-webkit-scrollbar {
    height: 4px;
  }
  
  .tab-menu::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
  }
  
  .tab-menu::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 2px;
  }
}
