/* DailyFortune.css */
.daily-fortune {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
}

.form-header,
.fortune-header {
  text-align: center;
  margin-bottom: 30px;
}

.form-header h2,
.fortune-header h2 {
  font-size: 2.5em;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.form-header p {
  font-size: 1.2em;
  opacity: 0.9;
  margin-bottom: 10px;
}

.fortune-date {
  font-size: 1.1em;
  opacity: 0.8;
  margin-bottom: 15px;
}

.fortune-form {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 1.1em;
  color: #ffffff;
}

.form-group input {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.9);
  color: #333;
  font-size: 16px;
  transition: all 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #fff;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.gender-group {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-weight: normal;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  transition: all 0.3s ease;
}

.radio-label:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.radio-label input[type="radio"] {
  width: auto;
  margin: 0;
}

.form-group small {
  display: block;
  margin-top: 8px;
  font-size: 0.9em;
  opacity: 0.8;
}

.submit-button {
  width: 100%;
  padding: 18px;
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1.3em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.submit-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.submit-button:disabled {
  opacity: 0.8;
  cursor: not-allowed;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 결과 화면 스타일 */
.fortune-content {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 30px;
}

.luck-scores {
  margin-bottom: 30px;
}

.luck-scores h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  text-align: center;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.scores-grid {
  display: grid;
  gap: 15px;
}

.luck-bar {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.luck-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 600;
}

.luck-score {
  font-size: 1.1em;
  color: #fff;
}

.luck-progress {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.luck-fill {
  height: 100%;
  border-radius: 4px;
  transition: width 1s ease-in-out;
  position: relative;
}

.luck-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.luck-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.info-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.info-card h3 {
  font-size: 1.3em;
  margin-bottom: 15px;
  color: #ffffff;
}

.lucky-color {
  font-size: 1.5em;
  font-weight: 700;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  display: inline-block;
}

.lucky-numbers {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.lucky-number {
  width: 50px;
  height: 50px;
  line-height: 50px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 50%;
  font-size: 1.3em;
  font-weight: 700;
  color: white;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

.today-message {
  margin-bottom: 30px;
  text-align: center;
}

.today-message h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
  color: #ffffff;
}

.message-text {
  font-size: 1.2em;
  line-height: 1.6;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.detailed-fortune h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
  text-align: center;
  color: #ffffff;
}

.fortune-text {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  line-height: 1.8;
  font-size: 1.1em;
  white-space: pre-wrap;
}

.fortune-line {
  margin-bottom: 10px;
  opacity: 0.95;
}

.fortune-line:empty {
  margin-bottom: 20px;
}

.action-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.try-again-button,
.share-button,
.back-button {
  padding: 15px 30px;
  border: none;
  border-radius: 25px;
  font-size: 1.1em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
  min-width: 150px;
}

.try-again-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.share-button {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

.back-button {
  background: linear-gradient(135deg, #ff9a56 0%, #ff6b9d 100%);
  margin-bottom: 20px;
}

.try-again-button:hover,
.share-button:hover,
.back-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.feature-info {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.feature-info h3 {
  margin-bottom: 15px;
  color: #ffffff;
  font-size: 1.3em;
}

.feature-info ul {
  list-style: none;
  padding: 0;
}

.feature-info li {
  padding: 8px 0;
  font-size: 1.1em;
  opacity: 0.9;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .daily-fortune {
    margin: 10px;
    padding: 15px;
  }
  
  .form-header h2,
  .fortune-header h2 {
    font-size: 2em;
  }
  
  .fortune-form,
  .fortune-content {
    padding: 20px;
  }
  
  .luck-info {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .gender-group {
    flex-direction: column;
    gap: 10px;
  }
  
  .radio-label {
    justify-content: center;
  }
  
  .action-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .try-again-button,
  .share-button,
  .back-button {
    width: 100%;
    max-width: 300px;
  }
  
  .lucky-numbers {
    flex-wrap: wrap;
  }
  
  .lucky-number {
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 1.1em;
  }
} 