/* ZodiacFortune.css */
.zodiac-fortune {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
}

.zodiac-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.zodiac-header h2 {
  font-size: 2.5em;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.zodiac-date {
  font-size: 1.1em;
  opacity: 0.8;
  margin-bottom: 15px;
}

.refresh-button {
  padding: 10px 20px;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.refresh-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.loading-container {
  text-align: center;
  padding: 50px;
}

.loading-container .spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.zodiac-content {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 30px;
}

.top-three {
  margin-bottom: 40px;
}

.top-three h3 {
  font-size: 2em;
  margin-bottom: 25px;
  text-align: center;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.podium {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 30px;
}

.podium-item {
  background: rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  padding: 25px;
  text-align: center;
  border: 2px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.podium-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #FFD700, #FFA500);
}

.podium-item.rank-1::before {
  background: linear-gradient(90deg, #FFD700, #FFA500);
}

.podium-item.rank-2::before {
  background: linear-gradient(90deg, #C0C0C0, #A0A0A0);
}

.podium-item.rank-3::before {
  background: linear-gradient(90deg, #CD7F32, #B8860B);
}

.podium-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.rank-badge {
  font-size: 3em;
  margin-bottom: 15px;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

.zodiac-info {
  margin-bottom: 20px;
}

.zodiac-emoji {
  font-size: 2.5em;
  margin-bottom: 10px;
}

.zodiac-name {
  font-size: 1.3em;
  font-weight: 700;
  margin-bottom: 8px;
  color: #ffffff;
}

.zodiac-score {
  font-size: 1.5em;
  font-weight: 800;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.zodiac-message {
  font-size: 1em;
  line-height: 1.4;
  opacity: 0.9;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.all-rankings h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  text-align: center;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.rankings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 15px;
  margin-bottom: 30px;
}

.ranking-item {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 15px;
}

.ranking-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.ranking-item.top-rank {
  border: 2px solid rgba(255, 215, 0, 0.5);
  background: rgba(255, 215, 0, 0.1);
}

.rank-number {
  font-size: 1.5em;
  font-weight: 800;
  min-width: 50px;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.zodiac-details {
  flex: 1;
}

.zodiac-header-item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.zodiac-header-item .zodiac-emoji {
  font-size: 1.5em;
  margin-bottom: 0;
}

.zodiac-header-item .zodiac-name {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 0;
  flex: 1;
}

.zodiac-header-item .zodiac-score {
  font-size: 1.2em;
  font-weight: 700;
}

.zodiac-message-small {
  font-size: 0.9em;
  opacity: 0.8;
  line-height: 1.3;
}

.zodiac-info-section {
  margin-top: 40px;
  padding-top: 30px;
  border-top: 2px solid rgba(255, 255, 255, 0.2);
}

.zodiac-info-section h3 {
  font-size: 1.8em;
  margin-bottom: 20px;
  text-align: center;
  color: #ffffff;
}

.info-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.info-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.info-card h4 {
  font-size: 1.3em;
  margin-bottom: 15px;
  color: #ffffff;
  text-align: center;
}

.info-card ul {
  list-style: none;
  padding: 0;
}

.info-card li {
  padding: 8px 0;
  font-size: 1em;
  opacity: 0.9;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.info-card li:last-child {
  border-bottom: none;
}

.action-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.share-button {
  padding: 15px 30px;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 1.2em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 200px;
}

.share-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .zodiac-fortune {
    margin: 10px;
    padding: 15px;
  }
  
  .zodiac-header h2 {
    font-size: 2em;
  }
  
  .zodiac-content {
    padding: 20px;
  }
  
  .podium {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .podium-item {
    padding: 20px;
  }
  
  .rank-badge {
    font-size: 2.5em;
  }
  
  .zodiac-emoji {
    font-size: 2em !important;
  }
  
  .rankings-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .ranking-item {
    padding: 15px;
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }
  
  .zodiac-header-item {
    justify-content: center;
  }
  
  .info-cards {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .share-button {
    width: 100%;
    max-width: 300px;
  }
}

/* 애니메이션 효과 */
.ranking-item {
  animation: fadeInUp 0.6s ease forwards;
  opacity: 0;
  transform: translateY(20px);
}

.ranking-item:nth-child(1) { animation-delay: 0.1s; }
.ranking-item:nth-child(2) { animation-delay: 0.2s; }
.ranking-item:nth-child(3) { animation-delay: 0.3s; }
.ranking-item:nth-child(4) { animation-delay: 0.4s; }
.ranking-item:nth-child(5) { animation-delay: 0.5s; }
.ranking-item:nth-child(6) { animation-delay: 0.6s; }

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.podium-item {
  animation: slideInUp 0.8s ease forwards;
  opacity: 0;
  transform: translateY(30px);
}

.podium-item:nth-child(1) { animation-delay: 0.2s; }
.podium-item:nth-child(2) { animation-delay: 0.4s; }
.podium-item:nth-child(3) { animation-delay: 0.6s; }

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
} 