.color-result-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  color: white;
  animation: fadeIn 0.8s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

.color-result-header {
  text-align: center;
  margin-bottom: 30px;
}

.color-result-header h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff6b6b, #feca57, #48dbfb, #ff9ff3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: rainbow 3s ease-in-out infinite;
}

@keyframes rainbow {
  0%, 100% { filter: hue-rotate(0deg); }
  50% { filter: hue-rotate(180deg); }
}

.result-main-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

.color-display {
  border-radius: 15px;
  padding: 30px;
  text-align: center;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
  position: relative;
  overflow: hidden;
}

.color-display::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: inherit;
  filter: blur(10px);
  opacity: 0.3;
  z-index: -1;
}

.color-info {
  position: relative;
  z-index: 1;
}

.color-name {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
}

.personality-type {
  font-size: 1.3rem;
  margin: 0;
  opacity: 0.9;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.description-section {
  margin-bottom: 30px;
}

.description-section h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #feca57;
}

.description {
  font-size: 1.1rem;
  line-height: 1.6;
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid #feca57;
}

.scores-section {
  margin-bottom: 30px;
}

.scores-section h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #48dbfb;
}

.scores-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
}

.score-item {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(255, 255, 255, 0.05);
  padding: 15px;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.score-item:hover {
  transform: translateY(-2px);
}

.score-color {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  flex-shrink: 0;
}

.score-color.blue { background: #3b82f6; }
.score-color.red { background: #ef4444; }
.score-color.green { background: #22c55e; }
.score-color.orange { background: #f97316; }

.score-label {
  flex: 1;
  font-size: 0.9rem;
}

.score-value {
  font-weight: bold;
  font-size: 1.1rem;
  color: #feca57;
}

.traits-section {
  margin-bottom: 30px;
}

.traits-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.strengths, .weaknesses {
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 10px;
}

.strengths h4 {
  color: #4ade80;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.weaknesses h4 {
  color: #fb7185;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.strengths ul, .weaknesses ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.strengths li, .weaknesses li {
  padding: 8px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.strengths li:last-child, .weaknesses li:last-child {
  border-bottom: none;
}

.strengths li::before {
  content: '✓ ';
  color: #4ade80;
  font-weight: bold;
}

.weaknesses li::before {
  content: '→ ';
  color: #fb7185;
  font-weight: bold;
}

.career-section, .compatibility-section {
  margin-bottom: 30px;
}

.career-section h4, .compatibility-section h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #a78bfa;
}

.career-tags, .compatibility-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.career-tag, .compatibility-tag {
  background: rgba(255, 255, 255, 0.1);
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.career-tag:hover, .compatibility-tag:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

.advice-section {
  margin-bottom: 30px;
}

.advice-section h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #fbbf24;
}

.advice {
  font-size: 1.1rem;
  line-height: 1.6;
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid #fbbf24;
  font-style: italic;
}

.result-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-bottom: 30px;
}

.share-button, .reset-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  color: white;
  padding: 15px 30px;
  border-radius: 25px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

.share-button:hover, .reset-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.3);
}

.share-button {
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
}

.reset-button {
  background: linear-gradient(135deg, #48dbfb 0%, #0abde3 100%);
}

.other-tests {
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 15px;
  text-align: center;
}

.other-tests h4 {
  font-size: 1.3rem;
  margin-bottom: 20px;
  color: #ff9ff3;
}

.other-tests-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
}

.test-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  transition: all 0.3s ease;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.test-card:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-3px);
}

.test-icon {
  font-size: 2rem;
  display: block;
  margin-bottom: 10px;
}

.test-name {
  font-size: 0.9rem;
  display: block;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .color-result-container {
    margin: 10px;
    padding: 15px;
    border-radius: 15px;
  }
  
  .color-result-header h2 {
    font-size: 2rem;
  }
  
  .result-main-card {
    padding: 20px;
  }
  
  .color-display {
    padding: 20px;
  }
  
  .color-name {
    font-size: 2rem;
  }
  
  .personality-type {
    font-size: 1.1rem;
  }
  
  .traits-grid {
    grid-template-columns: 1fr;
  }
  
  .result-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .share-button, .reset-button {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .color-result-container {
    margin: 5px;
    padding: 10px;
  }
  
  .color-result-header h2 {
    font-size: 1.8rem;
  }
  
  .result-main-card {
    padding: 15px;
  }
  
  .color-display {
    padding: 15px;
  }
  
  .color-name {
    font-size: 1.8rem;
  }
  
  .scores-grid {
    grid-template-columns: 1fr;
  }
  
  .other-tests-grid {
    grid-template-columns: 1fr;
  }
  
  .career-tags, .compatibility-tags {
    justify-content: center;
  }
}

/* 접근성 */
.share-button:focus, .reset-button:focus, .test-card:focus {
  outline: 3px solid rgba(255, 255, 255, 0.6);
  outline-offset: 2px;
}

/* 다크 모드 지원 */
@media (prefers-color-scheme: dark) {
  .color-result-container {
    background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
  }
  
  .result-main-card {
    background: rgba(255, 255, 255, 0.05);
  }
} 