/* SajuForm.css */
.saju-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
}

.form-header {
  text-align: center;
  margin-bottom: 30px;
}

.form-header h2 {
  font-size: 2.5em;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.form-header p {
  font-size: 1.2em;
  opacity: 0.9;
}

.saju-form-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 1.1em;
  color: #ffffff;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.9);
  color: #333;
  font-size: 16px;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #fff;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.form-group input.error,
.form-group select.error {
  border-color: #ff6b6b;
  background: rgba(255, 107, 107, 0.1);
}

.gender-group {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-weight: normal;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  transition: all 0.3s ease;
}

.radio-label:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.radio-label input[type="radio"] {
  width: auto;
  margin: 0;
}

.date-group {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 15px;
}

.time-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.form-group small {
  display: block;
  margin-top: 8px;
  font-size: 0.9em;
  opacity: 0.8;
}

.error-message {
  display: block;
  color: #ff6b6b;
  font-size: 0.9em;
  margin-top: 5px;
  font-weight: 500;
}

.submit-button {
  width: 100%;
  padding: 18px;
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1.3em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.submit-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.submit-button:disabled {
  opacity: 0.8;
  cursor: not-allowed;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.form-info {
  margin-top: 30px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.form-info h3 {
  margin-bottom: 15px;
  color: #ffffff;
  font-size: 1.3em;
}

.form-info ul {
  list-style: none;
  padding: 0;
}

.form-info li {
  padding: 8px 0;
  font-size: 1.1em;
  opacity: 0.9;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .saju-form {
    margin: 10px;
    padding: 15px;
  }
  
  .form-header h2 {
    font-size: 2em;
  }
  
  .saju-form-container {
    padding: 20px;
  }
  
  .date-group {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .time-group {
    grid-template-columns: 1fr;
  }
  
  .gender-group {
    flex-direction: column;
    gap: 10px;
  }
  
  .radio-label {
    justify-content: center;
  }
} 