.color-test-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  color: white;
  min-height: 600px;
}

.color-test-header {
  text-align: center;
  margin-bottom: 30px;
}

.color-test-header h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff6b6b, #feca57, #48dbfb, #ff9ff3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: rainbow 3s ease-in-out infinite;
}

@keyframes rainbow {
  0%, 100% { filter: hue-rotate(0deg); }
  50% { filter: hue-rotate(180deg); }
}

.color-test-header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin: 0;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 20px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #ff6b6b, #feca57, #48dbfb, #ff9ff3);
  border-radius: 4px;
  transition: width 0.5s ease;
}

.question-counter {
  text-align: center;
  font-size: 1.1rem;
  margin-bottom: 30px;
  opacity: 0.8;
}

.question-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 30px;
  margin-bottom: 30px;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.question-text {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 30px;
  line-height: 1.6;
}

.options-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.option-button {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 20px;
  color: white;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  position: relative;
  overflow: hidden;
}

.option-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  transition: left 0.5s ease;
}

.option-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.2);
  border-color: rgba(255, 255, 255, 0.4);
}

.option-button:hover::before {
  left: 100%;
}

.option-button.blue:hover {
  background: linear-gradient(135deg, #3b82f6, #1e40af);
  border-color: #60a5fa;
}

.option-button.red:hover {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  border-color: #f87171;
}

.option-button.green:hover {
  background: linear-gradient(135deg, #22c55e, #16a34a);
  border-color: #4ade80;
}

.option-button.orange:hover {
  background: linear-gradient(135deg, #f97316, #ea580c);
  border-color: #fb923c;
}

.test-footer {
  text-align: center;
  margin-top: 30px;
  opacity: 0.8;
}

.test-footer p {
  margin: 0;
  font-size: 1rem;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .color-test-container {
    margin: 10px;
    padding: 15px;
    border-radius: 15px;
  }
  
  .color-test-header h2 {
    font-size: 2rem;
  }
  
  .color-test-header p {
    font-size: 1rem;
  }
  
  .question-container {
    padding: 20px;
  }
  
  .question-text {
    font-size: 1.3rem;
  }
  
  .option-button {
    padding: 15px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .color-test-container {
    margin: 5px;
    padding: 10px;
  }
  
  .color-test-header h2 {
    font-size: 1.8rem;
  }
  
  .question-container {
    padding: 15px;
  }
  
  .question-text {
    font-size: 1.2rem;
  }
  
  .option-button {
    padding: 12px;
    font-size: 0.9rem;
  }
}

/* 접근성 */
.option-button:focus {
  outline: 3px solid rgba(255, 255, 255, 0.6);
  outline-offset: 2px;
}

/* 다크 모드 지원 */
@media (prefers-color-scheme: dark) {
  .color-test-container {
    background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
  }
  
  .option-button {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.1);
  }
} 