/* AdSense.css */
.adsense-container {
  margin: 20px 0;
  padding: 10px;
  text-align: center;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.ad-label {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* 더미 광고 스타일 (개발 모드) */
.dummy-ad {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  padding: 20px;
  color: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  margin: 0 auto;
}

.ad-content {
  padding: 10px 0;
}

.ad-content h3 {
  font-size: 1.3em;
  margin-bottom: 10px;
  color: #fff;
}

.ad-content p {
  font-size: 1em;
  margin-bottom: 15px;
  opacity: 0.9;
}

.ad-button {
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 1em;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

.ad-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* 실제 애드센스 스타일 */
.adsbygoogle {
  min-height: 100px;
  border-radius: 10px;
  overflow: hidden;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .adsense-container {
    margin: 15px 0;
    padding: 8px;
  }
  
  .dummy-ad {
    padding: 15px;
    max-width: 100%;
  }
  
  .ad-content h3 {
    font-size: 1.1em;
  }
  
  .ad-content p {
    font-size: 0.9em;
  }
  
  .ad-button {
    padding: 8px 16px;
    font-size: 0.9em;
  }
}

/* 애드센스 로딩 애니메이션 */
.adsbygoogle:empty {
  background: linear-gradient(90deg, #f0f0f0 25%, transparent 37%, #f0f0f0 63%);
  background-size: 400% 100%;
  animation: loading 1.4s ease infinite;
}

@keyframes loading {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: -100% 50%;
  }
} 