/* WeeklyFortune.css */
.weekly-fortune {
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  color: white;
  animation: fadeIn 0.8s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

.weekly-fortune.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.weekly-header {
  text-align: center;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
}

.weekly-header h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff6b6b, #feca57, #48dbfb, #ff9ff3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: rainbow 3s ease-in-out infinite;
}

@keyframes rainbow {
  0%, 100% { filter: hue-rotate(0deg); }
  50% { filter: hue-rotate(180deg); }
}

.week-period {
  font-size: 1.2rem;
  margin-bottom: 15px;
  opacity: 0.9;
}

.overall-score {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px 25px;
  border-radius: 25px;
  margin-top: 15px;
}

.score-label {
  font-size: 1.1rem;
  opacity: 0.8;
}

.score-value {
  font-size: 1.8rem;
  font-weight: bold;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
}

.weekly-theme {
  text-align: center;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
}

.theme-icon {
  font-size: 3rem;
  margin-bottom: 15px;
  display: block;
}

.weekly-theme h3 {
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #feca57;
}

.weekly-theme p {
  font-size: 1.1rem;
  opacity: 0.9;
  line-height: 1.6;
}

.weekly-summary {
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
}

.weekly-summary h4 {
  font-size: 1.4rem;
  margin-bottom: 15px;
  color: #48dbfb;
}

.weekly-summary p {
  font-size: 1.1rem;
  line-height: 1.6;
  opacity: 0.9;
}

.daily-fortunes {
  margin-bottom: 30px;
}

.daily-fortunes h4 {
  font-size: 1.4rem;
  margin-bottom: 20px;
  color: #a78bfa;
  text-align: center;
}

.daily-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 15px;
}

.daily-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 15px;
  text-align: center;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.daily-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.2);
  background: rgba(255, 255, 255, 0.15);
}

.daily-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 0.9rem;
  opacity: 0.8;
}

.day-name {
  font-weight: bold;
}

.daily-score {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 8px;
  text-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.daily-aspect {
  font-size: 0.9rem;
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 8px;
  border-radius: 10px;
  margin-bottom: 10px;
  display: inline-block;
}

.daily-message {
  font-size: 0.85rem;
  line-height: 1.4;
  opacity: 0.9;
  margin: 0;
}

.weekly-details {
  display: grid;
  grid-template-columns: 1fr;
  gap: 25px;
  margin-bottom: 30px;
}

.weekly-tips, .lucky-items, .weekly-cautions {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
}

.weekly-tips h4, .lucky-items h4, .weekly-cautions h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
}

.weekly-tips h4 {
  color: #4ade80;
}

.lucky-items h4 {
  color: #fbbf24;
}

.weekly-cautions h4 {
  color: #fb7185;
}

.weekly-tips ul, .weekly-cautions ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.weekly-tips li, .weekly-cautions li {
  padding: 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 1rem;
  line-height: 1.5;
}

.weekly-tips li:last-child, .weekly-cautions li:last-child {
  border-bottom: none;
}

.lucky-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.lucky-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  padding: 12px 16px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.lucky-label {
  font-size: 0.9rem;
  opacity: 0.8;
}

.lucky-value {
  font-weight: bold;
  font-size: 1rem;
  color: #feca57;
}

.weekly-footer {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 25px;
}

.refresh-button {
  background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
  border: none;
  color: white;
  padding: 12px 30px;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
  margin-bottom: 15px;
}

.refresh-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.3);
}

.disclaimer {
  font-size: 0.9rem;
  opacity: 0.7;
  margin: 0;
  line-height: 1.4;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .weekly-fortune {
    margin: 10px;
    padding: 20px;
    border-radius: 15px;
  }
  
  .weekly-header h2 {
    font-size: 2rem;
  }
  
  .overall-score {
    flex-direction: column;
    gap: 10px;
  }
  
  .theme-icon {
    font-size: 2.5rem;
  }
  
  .weekly-theme h3 {
    font-size: 1.5rem;
  }
  
  .daily-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 10px;
  }
  
  .daily-card {
    padding: 12px;
  }
  
  .daily-score {
    font-size: 1.3rem;
  }
  
  .lucky-grid {
    grid-template-columns: 1fr;
  }
  
  .weekly-details {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .weekly-fortune {
    margin: 5px;
    padding: 15px;
  }
  
  .weekly-header h2 {
    font-size: 1.8rem;
  }
  
  .weekly-header, .weekly-theme, .weekly-summary, 
  .weekly-tips, .lucky-items, .weekly-cautions, .weekly-footer {
    padding: 20px;
  }
  
  .daily-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .daily-card {
    padding: 10px;
  }
  
  .daily-message {
    font-size: 0.8rem;
  }
  
  .refresh-button {
    padding: 10px 25px;
    font-size: 1rem;
  }
}

/* 태블릿 가로 모드 */
@media (min-width: 769px) and (max-width: 1024px) {
  .daily-grid {
    grid-template-columns: repeat(7, 1fr);
  }
  
  .weekly-details {
    grid-template-columns: 1fr 1fr;
  }
  
  .weekly-cautions {
    grid-column: span 2;
  }
}

/* 데스크톱 큰 화면 */
@media (min-width: 1025px) {
  .daily-grid {
    grid-template-columns: repeat(7, 1fr);
  }
  
  .weekly-details {
    grid-template-columns: 1fr 1fr;
  }
  
  .weekly-cautions {
    grid-column: span 2;
  }
  
  .lucky-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* 접근성 */
.refresh-button:focus {
  outline: 3px solid rgba(255, 255, 255, 0.6);
  outline-offset: 2px;
}

.daily-card:focus {
  outline: 2px solid rgba(255, 255, 255, 0.5);
  outline-offset: 2px;
}

/* 다크 모드 지원 */
@media (prefers-color-scheme: dark) {
  .weekly-fortune {
    background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
  }
  
  .weekly-header, .weekly-theme, .weekly-summary,
  .weekly-tips, .lucky-items, .weekly-cautions, .weekly-footer {
    background: rgba(255, 255, 255, 0.05);
  }
  
  .daily-card {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.1);
  }
  
  .lucky-item {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.1);
  }
} 